import React from "react";
import "./Intro.css";
import Vector1 from "../../imgs/Vector1.png";
import Vector2 from "../../imgs/Vector2.png";
import boy from "../../imgs/boy.png";
import glassesimoji from "../../imgs/glassesimoji.png";
import thumbup from "../../imgs/thumbup.png";
import crown from "../../imgs/crown.png";
import FloatinDiv from "../FloatingDiv/FloatingDiv";
import Github from "../../imgs/github.png";
import LinkedIn from "../../imgs/linkedin.png";
import Instagram from "../../imgs/instagram.png";
import { motion } from "framer-motion";
import { Link } from "react-scroll";
import { useSelector } from "react-redux";
const Intro = () => {
  // Transition
  const transition = { duration: 2, type: "spring" };

  // state
  const {mode}=useSelector((state)=>state.darkMode);

  return (
    <div className="Intro" id="Intro">
      {/* left name side */}
      <div className="i-left">
        <div className="i-name">
          {/* yahan change hy darkmode ka */}
          <span style={{ color: mode ? "white" : "" }}>Hy ! I Am</span>
          <span>Mohamed Mostafa</span>
          <span>
            Frontend Developer
          </span>
        </div>
        <Link to="contact" smooth={true} spy={true}>
          <button className="button i-button">Hire me</button>
        </Link>
        {/* social icons */}
        <div className="i-icons">
         <a target="_blank" href="https://github.com/mm341"><img src={Github} alt="" /></a> 
          <a target="_blank" href="https://www.linkedin.com/in/mohamed-mostafa-688845151/"><img src={LinkedIn} alt="" /></a>
         <a target="_blank" href="https://www.instagram.com/medo_geologist1234/"><img src={Instagram} alt="" /></a> 
        </div>
      </div>
      {/* right image side */}
      <div className="i-right">
        <img src={Vector1} alt="" />
        <img src={Vector2} alt="" />
        <img src={boy} alt="" />
        {/* animation */}
        <motion.img
          initial={{ left: "-36%" }}
          whileInView={{ left: "-24%" }}
          transition={transition}
          src={glassesimoji}
          alt=""
        />

        <motion.div
          initial={{ top: "-4%", left: "74%" }}
          whileInView={{ left: "68%" }}
          transition={transition}
          className="floating-div"
        >
          <FloatinDiv img={crown} text1="Web" text2="Developer" />
        </motion.div>

        {/* animation */}
        <motion.div
          initial={{ left: "9rem", top: "18rem" }}
          whileInView={{ left: "0rem" }}
          transition={transition}
          className="floating-div"
        >
          {/* floatinDiv mein change hy dark mode ka */}
          <FloatinDiv img={thumbup} text1="Best Design" text2="Award" />
        </motion.div>

        <div className="blur" style={{ background: "rgb(238 210 255)" }}></div>
        <div
          className="blur"
          style={{
            background: "#C1F5FF",
            top: "17rem",
            width: "21rem",
            height: "11rem",
            left: "-9rem",
          }}
        ></div>
      </div>
    </div>
  );
};

export default Intro;
